<template>
    <div>
        <div class="banner" @click="handleBannerClick">
            <img class="banner-img" :src="bannerImg" alt="">
            <div class="banner-info">
                <div class="banner-title">{{this.sightName}}</div>
                <div class="banner-number">
                    <sapn class="iconfont banner-icon">&#xe626;</sapn>{{this.bannerImgs.length}}
                </div>
            </div>
        </div>
        <fade-animation>
            <common-gallery :imgs="bannerImgs" v-show="showGallery" @close="handleGalleryClose"></common-gallery>
        </fade-animation>
    </div>

</template>

<script>
    import CommonGallery from 'common/gallery/Gallery'
    import FadeAnimation from 'common/fade/FadeAnimation'
    export default {
        name: "DetailBanner",
        props:{
            sightName:String,
            bannerImg:String,
            bannerImgs:Array
        },
        data(){
            return {
                showGallery:false,
            }

        },
        methods:{
            handleBannerClick(){
                this.showGallery=true
            },
            handleGalleryClose(){
                this.showGallery=false
            }
        },
        components:{
            CommonGallery,
            FadeAnimation
        }
    }
</script>

<style scoped>
    .banner{
        position: relative;

        overflow: hidden;
        height: 0;
        padding-bottom: 55%;
    }
    .banner-img{
        width: 100% ;
    }
    .banner-info{
        display: flex;
        position: absolute;
        left:0;
        right:0;
        bottom:0;
        line-height: 0.6rem;
        color:#fff;
        background-image: linear-gradient(top,rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
    }
    .banner-title{
        flex:1;
        font-size: 0.32rem;
        padding:0 0.2rem;
    }
    .banner-number{
        padding:0 0.4rem;
        height: 0.32rem;
        line-height: 0.32rem;
        margin-top: 0.14rem;
        border-radius: 0.2rem;
        background: rgba(0,0,0,0.8);
        font-size: 0.24rem;
    }
    .banner-icon{
        font-size: 0.24rem;
    }
</style>


